<template>
  <div class="container">
    <h2 class="text-center">測驗大廳</h2>
    <!-- 訊息提示框 -->
    <Alert :MODE="MSG.MODE" :MSGTYPE="MSG.TYPE" :TITLE="MSG.TITLE" :TEXT="MSG.TEXT" @alert-check="(sure) => (alertCheck = sure)" />
    <p class="fst-italic">更新時間：{{ updateTime }}</p>
    <form class="row">
      <div class="col-12">
        <h3 class="mb-3">◎ 測驗科目</h3>
      </div>
      <div class="col-12">
        <div class="form-check form-check-inline">
          <input class="form-check-input" type="radio" name="examTypeRadioOptions" id="examTypeRadio1" value="準則" checked v-model="examType" />
          <label class="form-check-label" for="examTypeRadio1">準則</label>
        </div>
        <div class="form-check form-check-inline">
          <input class="form-check-input" type="radio" name="examTypeRadioOptions" id="examTypeRadio2" value="學科" v-model="examType" />
          <label class="form-check-label" for="examTypeRadio2">學科</label>
        </div>
      </div>
      <div class="col-12">
        <h3 class="my-3">◎ 測驗題組</h3>
      </div>
      <div class="col-12" v-if="questionGroupData.length !== 0">
        <div class="form-check" v-for="item in questionGroup">
          <input class="form-check-input" type="checkbox" :value="item.QGID" :id="item.QGID" v-model="exam_QGID_select" />
          <label class="form-check-label" :for="item.QGID">{{ item.subject }}</label>
        </div>
      </div>
      <div class="col-12" v-else>
        <p class="text-warning bg-dark text-center">**目前尚未有題組資料**</p>
      </div>
      <div class="col-12">
        <h3 class="my-3">◎ 測驗模式</h3>
      </div>
      <div class="col-12">
        <div class="form-check form-check-inline">
          <input class="form-check-input" type="radio" name="modeRadioOptions" id="modeRadio1" value="K書模式" checked v-model="examMode" />
          <label class="form-check-label" for="modeRadio1">K書模式</label>
        </div>
        <div class="form-check form-switch form-check-inline">
          <input class="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckChecked" v-model="isRandom" />
          <label class="form-check-label" for="flexSwitchCheckChecked">打亂選項</label>
        </div>
      </div>
      <div class="col-12">
        <div class="form-check">
          <input class="form-check-input" type="radio" name="modeRadioOptions" id="modeRadio2" value="模擬測驗" v-model="examMode" />
          <label class="form-check-label" for="modeRadio2">模擬測驗</label>
        </div>
        <div class="form-check">
          <input class="form-check-input" type="radio" name="modeRadioOptions" id="modeRadio3" value="弱點測驗" disabled v-model="examMode" />
          <label class="form-check-label" for="modeRadio3">弱點測驗(開發中)</label>
        </div>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-primary btn-lg" type="button" @click="startExam()" v-bind:disabled="questionGroupData.length === 0">開始測驗</button>
      </div>
    </form>
  </div>
</template>

<script>
import router from "@/router";
import Alert from "@/components/Alert.vue";

export default {
  name: "ExamLobby",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 題庫更新時間
      updateTime: "",
      // 總測驗科目
      questionGroupData: [],
      // 分類過後的測驗科目_準則_學科
      questionGroup: [],
      // 使用者選擇的測驗參數
      examType: "準則",
      examMode: "K書模式",
      isRandom: false,
      exam_QGID_select: [],
    };
  },
  methods: {
    startExam() {
      // 請求後端取得測驗題目
      let examData = {};
      if (this.exam_QGID_select.length === 0) {
        // 訊息提示框
        this.alertVisible = true;
        this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
        this.MSG.TYPE = "warning"; // primary成功, warning警告, danger錯誤
        this.MSG.TITLE = "系統警告"; // 系統提示，系統警告，系統錯誤
        this.MSG.TEXT = ["請選擇至少1組準則或學科。"]; // 內容
        return;
      }
      this.$http
        .post("api/questionExam/productExam", {
          questionGroupID: this.exam_QGID_select,
          examType: this.examType,
          examMode: this.examMode,
          isRandom: this.isRandom,
        })
        .then((res) => {
          examData = res.data.data;
          examData.examType = this.examType;
          examData.examMode = this.examMode;
          examData.isRandom = this.isRandom;
          examData.exam_QGID_list = this.exam_QGID_select;
          examData.TFQ.forEach((item) => {
            item.answer = "";
            item.result = false;
            item.weight = 0;
          });
          examData.SCQ.forEach((item) => {
            item.correctIndex = item.options.indexOf(item.correct) + 1;
            item.answer = "";
            item.answerIndex = "";
            item.result = false;
            item.weight = 0;
          });
          examData.MCQ.forEach((item) => {
            item.correctIndex = [];
            item.correct.forEach((correct) => {
              item.correctIndex.push(item.options.indexOf(correct) + 1);
            });
            item.correctIndex.sort((a, b) => a - b);
            item.answer = [];
            item.answerIndex = [];
            item.result = false;
            item.weight = 0;
          });
          // 將資料存入Vuex
          this.$store.commit("SET_EXAMINATION", examData);
          router.push("/exam");
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    // 取得提庫更新時間
    this.$http
      .get("api/questionManage/getUpdateTime")
      .then((res) => {
        this.updateTime = new Date(res.data.data).toLocaleString();
      })
      .catch((err) => {
        console.log(err);
      });
    // 取得資料庫中的題組資料
    this.$http
      .get("api/questionManage/getQuestionGroupList")
      .then((res) => {
        this.questionGroupData = res.data.data;
        // 預設準則題組之畫面資料
        this.questionGroup = this.questionGroupData.filter((item) => item.type === "準則");
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    // 監聽examType的變化，如果是準則，從questionGroupData中取得準則題組
    examType(newVal) {
      if (newVal === "準則") {
        this.questionGroup = this.questionGroupData.filter((item) => item.type === "準則");
      } else {
        this.questionGroup = this.questionGroupData.filter((item) => item.type === "學科");
      }
    },
    // 監聽examMode，若為模擬測驗，則打亂選項為true
    examMode(newVal) {
      if (newVal === "模擬測驗") {
        this.isRandom = true;
      } else {
        this.isRandom = false;
      }
    },
  },
};
</script>

<style lang="scss"></style>
